import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { Hp, Hm, Hs, Nt, Ccm, Ksa, Footerlogo, Pointer } from "../assets/img/image";

const FooterContainer = styled.div`
    height: 390.5px;
    width: 100%;
    margin-top: 120px;
    border-top: 1px solid orange;
    .policy {
        width: 1200px;
        margin: auto;
        padding: 10px 0;
        ul {
            display: flex;
            width: fit-content;
            margin: auto;
            li {
                margin: 20px 25px;
                a {
                    font-size: 14px;
                    letter-spacing: -0.08em;
                }
            }
        }
    }
    .brand_family {
        background-color: #f9f8f7;
        height: 75px;
        ul {
            width: fit-content;
            display: flex;
            margin: auto;
            li {
                margin: 23px 25px;
                select {
                    padding: 7px 10px;
                    margin-left: 30px;
                    font-size: 11px;
                    width: 150px;
                    border-radius: 5px;
                    border: 1px solid #b1ab9f;
                    color: #b1ab9f;
                    box-shadow: 2px 2px #d0cecc;
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    background: url(${Pointer}) no-repeat 97% 50%/15px auto;
                    background-color: white;
                    appearance: none;
                    &:focus {
                        outline: none;
                    }
                }
                img {
                    height: 31px;
                }
            }
        }
    }
    .logo {
        width: fit-content;
        margin: auto;
        padding: 40px 0 30px 0;
    }
    .info {
        width: fit-content;
        margin: auto;
        p {
            font-size: 11px;
            text-align: center;
            letter-spacing: -.05em;
            padding-bottom: 10px;
            span {
                margin: 0 7px;
            }
        }
    }
    footer {
        padding-top: 20px;
        width: fit-content;
        margin: auto;
        font-size: 11px;
        letter-spacing: -.05em;
        color: #b1ab9f;
    }
`;

const Footer = () => {
    return (
        <FooterContainer>
            <div className="policy">
                <ul>
                    <li>
                        <Link to="/"></Link>
                    </li>
                    <li>
                        <Link to="/"></Link>
                    </li>
                    <li>
                        <Link to="/"></Link>
                    </li>
                    <li>
                        <Link to="/"></Link>
                    </li>
                    <li>
                        <Link to="/">
                            <span></span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/"></Link>
                    </li>
                    <li>
                        <Link to="/"> </Link>
                    </li>
                </ul>
            </div>
            <div className="brand_family">
                <ul>
                    <li>
                        <Link to="/">
                            <img src={Hp} alt="happy point" />
                        </Link>
                    </li>
                    <li>
                        <Link to="/">
                            <img src={Hm} alt="happy market" />
                        </Link>
                    </li>
                    <li>
                        <Link to="/">
                            <img src={Hs} alt="happy story" />
                        </Link>
                    </li>
                    <li>
                        <Link to="/">
                            <img src={Nt} alt="Norton" />
                        </Link>&nbsp;&nbsp;

                        <Link to="/">
                            <img src={Ccm} alt=" " />
                        </Link>&nbsp;&nbsp;

                        <Link to="/">
                            <img src={Ksa} alt="ksa" />
                        </Link>
                    </li>
                    <li>
                        <select>
                            <option value="familysite">
                                FAMILY SITE
                            </option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=" "> </option>
                            <option value="BR">BR</option>
                            <option value="SPC MAGAZINE">SPC MAGAZINE</option>
                            <option value="SPC">SPC</option>
                            <option value=""></option>
                        </select>
                    </li>
                </ul>
            </div>
            <div className="logo">
                <img src={Footerlogo} alt="baskin robbins" />
            </div>
            <div className='info'>
                <p>
                    <span> :303-81-09535</span>
                    <span>()  </span>
                    <span>   2620( 11-149)</span>
                </p>
                <p>
                    TEL: 080-555-3131
                    <span>: </span>
                </p>
            </div>
            <footer>Copyright&copy;2016BRKOREA Company.All Rights Reserved.</footer>
        </FooterContainer>
    );
};

export default Footer;
